@import url(https://fonts.googleapis.com/css2?family=Damion&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header {
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background: url(/static/media/Paw-MatchingPaws-18.eb9adf96.png);
    background-size: cover;
    background-position: center;
}

h2 {
    margin: 2px 0 0 0;
    font-size: 24px;
    color: white;
    text-align: center;
    /* text-shadow: 2px 2px black; */
}

span span { 
    position: relative;
    opacity: 0;
    color: white; 
    animation: move-text 0.3s forwards;
    font-size: 75px;
    font-weight: bold;
    font-style: italic;
    text-shadow: 2px 2px black;
}

@keyframes move-text {
    0% { bottom: -0.1em; opacity: 1; }
    50% { bottom: 0.1em; }
    100% { bottom: 0; opacity: 1; }
}

@media screen and (max-width: 820px) {
    .header {
        height: 100vh;
        /* background: url('https://images.unsplash.com/photo-1546447147-3fc2b8181a74?ixlib=rb-1.2.1&ixid=MXwxMjA3fDB8MHxwaG90by1yZWxhdGVkfDd8fHxlbnwwfHx8&auto=format&fit=crop&w=800&q=60'); */
        background: url(/static/media/paw.fb0fe0f2.png);
        background-size: cover;
        background-position: center;
        display: flex;
        justify-content: center;
        padding: 0 40px;
    }

    .header h2 {
        margin: 2px 0 0 0;
        font-size: 14px;
        color: black;
        text-align: center;
    }

    span span { 
        position: relative;
        opacity: 0;
        color: white; 
        animation: move-text 0.3s forwards;
        font-size: 45px;
        font-weight: bold;
        font-style: italic;
        text-shadow: 2px 2px black;
    }
}
a {
    text-decoration: none;
    color: #232323;
    transition: color 0.3s ease;
}

#navbarHamburger {
    display: none;
}

.navbar {
    width: 100%;
    height: 10vh;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    position: absolute;
}

.navbar-logo p {
  padding: 0 10px;
  color: black;
  font-size: 20px;
  font-weight: bold;
}

.header-menu {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.header-menu a {
    margin: 0 15px;
    color: #ffffff;
}

.navbar-logo {
  display: flex;
  align-items: center;
  padding: 0 10px;
}

.logo {
  width: 30px;
  height: 30px;
}

/* MOBILE */
@media screen and (max-width: 720px) {
    .header-menu {
        display: none;
    }

    #navbarHamburger {
        display: block;
        position: fixed;
        top: 20px;
        z-index: 1;
        -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
      }
      
      #navbarHamburger input {
        display: block;
        width: 40px;
        height: 32px;
        position: absolute;
        top: -7px;
        left: 85vw;
        cursor: pointer;
        opacity: 0; 
        z-index: 2; 
        -webkit-touch-callout: none;
      }

      #navbarHamburger span {
        display: block;
        width: 33px;
        height: 4px;
        margin-bottom: 5px;
        position: relative;
        left: 85vw;
        background: #cdcdcd;
        border-radius: 3px;
        z-index: 1;
        transform-origin: 4px 0px;       
        transition: transform 0.5s cubic-bezier(0.77,0.2,0.05,1.0),
                    background 0.5s cubic-bezier(0.77,0.2,0.05,1.0),
                    opacity 0.55s ease;
      }
      
      #navbarHamburger span:first-child {
        transform-origin: 0% 0%;
      }
      
      #navbarHamburger span:nth-last-child(2) {
        transform-origin: 0% 100%;
      }
      
      #navbarHamburger input:checked ~ span {
        opacity: 1;
        transform: rotate(45deg) translate(-2px, -1px);
        background: #232323;
      }
      
      #navbarHamburger input:checked ~ span:nth-last-child(3) {
        opacity: 0;
        transform: rotate(0deg) scale(0.2, 0.2);
      }

      #navbarHamburger input:checked ~ span:nth-last-child(2) {
        transform: rotate(-45deg) translate(0, -1px);
      }
      
      #menu {
        position: absolute;
        width: 100vw;
        height: 100vh;
        margin: -100px 0 0 -50px;
        padding: 50px;
        padding-top: 125px;
        text-align:center;     
        background: #ededed;
        list-style-type: none;
        -webkit-font-smoothing: antialiased;
        /* to stop flickering of text in safari */     
        transform-origin: 0% 0%;
        transform: translate(0, -100%);      
        transition: transform 0.5s cubic-bezier(0.77,0.2,0.05,1.0);
      }
      
      #menu li {
        padding: 10px 0;
        font-size: 22px;
      }

      #navbarHamburger input:checked ~ ul {
        transform: none;
      }
}



#root {
  box-sizing: border-box;
}

body {
  box-sizing: border-box;
}
